import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import './assets/swiper/swiper.min.css'
import './assets/swiper/swiper.min.js'
// import './assets/js/common.js?_t=_0.7.5'

const { mockXHR } = require('../mock').default
mockXHR()

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

// import VueAwesomeSwiper from 'vue-awesome-swiper'
// import 'swiper/dist/css/swiper.css'
// Vue.use(VueAwesomeSwiper)

import VideoPlayer from 'vue-video-player'
import 'video.js/dist/video-js.css'
import 'vue-video-player/src/custom-theme.css'
Vue.use(VideoPlayer)

import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload, {
  // 
  preLoad: 1,
  // 
  error: require('./assets/img/error.png'),
  // GIF
  loading: require('./assets/img/loading.gif'),
  // 
  attempt: 1
})

Vue.config.productionTip = false

// 
axios.defaults.timeout = 5000
// api
// axios
axios.defaults.baseURL = 'http://shkjgw.shkjem.com/api/'
Vue.prototype.$http = axios

// 
Vue.prototype.imgserver = 'http://shkjgw.shkjem.com/'

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
