import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const feiyibanrouter = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('./views/Home.vue')
    },
    {
      path: '/price_list',
      name: 'price',
      component: () => import('./views/Price.vue')
    },
    {
      path: '/workwx',
      name: 'workwx',
      component: () => import('./views/product/Workwx.vue')
    },
    {
      path: '/add_fans',
      name: 'add_fans',
      component: () => import('./views/scheme/AddFans.vue')
    },
    {
      path: '/solution_live',
      name: 'solution_live',
      component: () => import('./views/scheme/SolutionLive.vue')
    }
  ]
})

//  
feiyibanrouter.beforeEach((to, from, next) => {
  // 
  if (to.matched.some(res => res.meta.requireAuth)) {
    // 
    if (sessionStorage.getItem('token')) {
      next()
    } else {
      // 
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    }
  } else {
    next()
  }
})

export default feiyibanrouter
